import { useCus } from './contexts/Cus'

const Config = () => {
  const {

    mat,
    setMat,
    buttonState,
    setButtonState,
  } = useCus()


  const handleButtonClick = (button) => {
    // Reset all buttons to default state
    setButtonState({ A: false, B: false, C: false });

    // Set the clicked button to active state
    setButtonState((prevState) => ({ ...prevState, [button]: true }));

    // Set mat color based on the clicked button
    if (button === 'A') {
      setMat('white');
    } else if (button === 'B') {
      setMat('blue');
    } else if (button === 'C') {
      setMat('red');
    }
  };

  return (
    <>


     

      <div className="block-container">
      <button
        className={`block-button1 ${buttonState.B ? 'active' : ''}`}
        onClick={() => handleButtonClick('B')}
      >
      </button>
      <button
        className={`block-button2 ${buttonState.C ? 'active' : ''}`}
        onClick={() => handleButtonClick('C')}
      >
      </button>
    </div>

      {/* Logo placed in the bottom left corner */}
      <div className="logo-container">
        <a
          href="https://www.joydesign3d.link"
          target="_blank"
          rel="noopener noreferrer">
          <img
            className="logo-img"
            src="/images/link_logo.png"
            alt="Logo"
            style={{ width: '100%' }}
          />
        </a>
      </div>
    </>
  )
}

export default Config
