import React from 'react'
import { useGLTF  } from '@react-three/drei'

import { useCus } from './contexts/Cus'


const Model = (props) => {

    const {

      mat,

    } = useCus();


  const { nodes, materials } = useGLTF('/shoes/shoes.gltf')

  return (
    <group {...props} dispose={null}>
      <mesh geometry={nodes.Shoes_L.geometry} material={ (mat === "blue") ? materials.Shoes_material_blue : materials.Shoes_material_red} />
      <mesh geometry={nodes.Shoes_R.geometry} material={(mat === "blue") ? materials.Shoes_material_blue : materials.Shoes_material_red} />
    </group>
  )
}

useGLTF.preload('/shoes/shoes.gltf')


export default Model;

