import {
  OrbitControls,
  PresentationControls,
  Stage
} from '@react-three/drei'

import Model from './Model'

const Case = (props) => {

  return (
    <>
      <PresentationControls
        global // Spin globally or by dragging the model
        speed={1.5} // Speed factor
        zoom={0.85} // Zoom factor when half the polar-max is reached
        polar={[-Math.PI / 2, Math.PI / 2]} // Vertical limits
        cursor={true}>
        <OrbitControls enableRotate={false} minDistance={0.05} maxDistance={8} />
        <group {...props} dispose={null}>
          <Stage intensity={0.9} shadows={true} adjustCamera={false} environment={'city'}>
            <directionalLight
              position={[5, 5, 5]}
              intensity={2}
              castShadow={true}
            />
            <Model position={[0, 0, 0]} />

          </Stage>
        </group>
      </PresentationControls>
    </>
  )
}

export default Case
