import { createContext, useContext, useState } from "react";

const CusContext = createContext({})

export const CusProvider = (props) => {
    const [mat, setMat] =useState("blue")
    const [buttonState, setButtonState] =useState(false)

    return (
        <CusContext.Provider value={{
            mat,
            setMat,
            buttonState,
            setButtonState,
        }}>
            {props.children}

        </CusContext.Provider>
    )
}

export const useCus = () => {
    const context = useContext(CusContext);
    return context;
    }