
import { Canvas } from '@react-three/fiber'
import Case from './Case'
import Config from './Config'
import { CusProvider } from './contexts/Cus'

export default function App() {
  return (
    <CusProvider>

    <div className='case'>

    <Canvas  camera={{ position: [0, 0.3, 0.5], near: 0.0001, fov:45 }} shadows>
      <color attach={"background"} args={["#606060"]} />
      <fog attach={"fog"} args={["#606060", 10, 30]} />
      
      <Case />
    </Canvas>
    </div>

    <Config />
    
    </CusProvider>
    
  )
}
